function onOk(callback) {
  if (typeof callback === 'function') {
    callback();
  }
  document.getElementById('weui_dialog_alert').style.display = 'none';
}
function Alert(content, title = '提示') {
  const template = `
    <div class="weui_dialog_alert" id="weui_dialog_alert">
      <div class="weui_mask"></div>
      <div class="weui_dialog">
          <div class="weui_dialog_hd">
            <strong class="weui_dialog_title">${title}</strong>
        </div>
          <div class="weui_dialog_bd">${content}</div>
          <div class="weui_dialog_ft">
              <a href="javascript:;" class="weui_btn_dialog primary" id="weui_dialog_alert_onOk">确定</a>
          </div>
      </div>
    </div>`;
  const alertId = document.getElementById('weui_dialog_alert');
  if (alertId) {
    alertId.querySelector('.weui_dialog_title').textContent = title;
    alertId.querySelector('.weui_dialog_bd').textContent = content;
    alertId.style.display = 'block';
  } else {
    document.body.insertAdjacentHTML('beforeend', template);
  }
  document.getElementById('weui_dialog_alert_onOk').addEventListener('click', onOk);
}

module.exports = Alert;
