const attachFastClick = require('fastclick');
document.body.addEventListener('touchstart', () => { });
attachFastClick(document.body);
const downloadAppInit = require('../component/downloadApp');
const util = {
  param(key) {
    const url = location.search.replace(/^\?/, '').split('&');
    const [paramsObj, iLen] = [{}, url.length];
    let i;
    for (i = 0; i < iLen; i++) {
      const param = url[i].split('=');
      paramsObj[param[0]] = param[1];
    }
    if (key) {
      return decodeURI(paramsObj[key]) || '';
    }
    return paramsObj;
  },
  pageComplete() {
    const pageLoaderElem = document.getElementById('pageLoader');
    const pageHiddenElem = document.getElementById('pageHidden');
    pageLoaderElem.querySelector('.square-spin').classList.add('loaderLeave');
    setTimeout(() => {
      document.body.classList.remove('pageLoader');
      pageHiddenElem.classList.remove('pageHidden');
      document.getElementById('pageTextHidden').style.cssText = '';
      pageHiddenElem.classList.add('pageEnter');
      pageLoaderElem.style.display = 'none';
    }, 500);
    setTimeout(() => {
      document.getElementById('contentBody').className = '';
      document.getElementById('contentBody').classList.remove('contentBody');
      pageHiddenElem.classList.remove('pageEnter');
    }, 1000);
    setTimeout(() => {
      downloadAppInit(util);
    }, 2000)
  },
  formData(params) {
    let [key, data] = ['', ''];
    for (key in params) {
      data += `&${key}=${params[key]}`;
    }
    return data.replace('&', '');
  },
  spriteAdaptation(elem, scale, spriteImgSize) {
    const width = spriteImgSize ? spriteImgSize[0] : window.spriteImgSize[0];
    const height = spriteImgSize ? spriteImgSize[1] : window.spriteImgSize[1];
    const bpx = parseInt(document.defaultView.getComputedStyle(elem, 'null').backgroundPositionX.replace('px', ''));
    const bpy = parseInt(document.defaultView.getComputedStyle(elem, 'null').backgroundPositionY.replace('px', ''));
    elem.style.backgroundPosition = `${Math.ceil(bpx * scale)}px ${Math.ceil(bpy * scale)}px`;
    elem.style.webkitBackgroundSize = `${Math.ceil(width * scale)}px ${Math.ceil(height * scale)}px`;
    elem.style.backgroundSize = `${Math.ceil(width * scale)}px ${Math.ceil(height * scale)}px`;
  },
  imgSuffix(src) {
    if (window.isWebpSupport) {
      return src.replace(/\.(jpg|gif|png)$/i, '.webp');
    }
    return src;
  },
  //获取元素的left值
  getElementLeft(element) {
    let actualLeft = element.offsetLeft;
    let current = element.offsetParent;
    while (current !== null) {
      actualLeft += current.offsetLeft;
      current = current.offsetParent;
    }
    return actualLeft;
  }
};

module.exports = util;
