/*global Swiper :true*/
/*eslint no-undef: "error"*/
let viewSwiper;
const winWidth = document.body.clientWidth;

const swiperUtil = {
  viewImg(length, swiperBox, imgUrlArray, viewImage, activeIndex) {
    let i;
    swiperBox.innerHTML = '';
    for (i = 0; i < length; i++) {
      swiperBox.insertAdjacentHTML('beforeend', `
        <div class="swiper-slide">
          <img class="img" src="${imgUrlArray[i]}">
        </div>
      `)
    }
    viewImage.classList.remove('hidden');
    if (!viewSwiper) {
      viewSwiper = new Swiper('#viewSwiper', {
        initialSlide: activeIndex,
        pagination: '.viewPagination'
      })
    } else {
      viewSwiper.reInit('#viewSwiper');
      viewSwiper.swipeTo(activeIndex, 0);
      viewSwiper.setWrapperTranslate(winWidth * activeIndex, 0, 0);
    }
    if (length > 1) {
      document.querySelector('.viewPagination').style.display = 'block';
    } else {
      document.querySelector('.viewPagination').style.display = 'none';
    }
    swiperBox.style.display = 'inline-block';
  },
  hiddenView(viewImage, swiperBox) {
    document.getElementById('mask').addEventListener('click', () => {
      viewImage.classList.add('hidden');
      swiperBox.innerHTML = '';
      document.querySelector('.viewPagination').innerHTML = '';
      swiperBox.style.display = 'none';
    });
  }
}

module.exports = swiperUtil;
