/*global Swiper :true*/
/*eslint no-undef: "error"*/
require('es6-promise').polyfill();
require('isomorphic-fetch');
const util = require('../lib/util');
const { apiUrl } = require('../common/config');
const Alert = require('../component/alert');
const swiperUtil = require('../lib/swiperUtil');

const serviceId = util.param('serviceId');
const tagScale = document.getElementById('hot').clientWidth / 70;
const tabItemElem = document.querySelector('.tabItem').querySelectorAll('li');
const tabLineElem = document.getElementById('tabLine');
const tabSlide = document.getElementById('businessService').querySelectorAll('.swiper-slide');
const downloadLink = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.linxin.lx';
const telWrapElem = document.getElementById('telWrap');
let i;

//设置图标缩放
util.spriteAdaptation(document.getElementById('businessIcon'), 0.5, window.spriteImgSize);

//商家图片
function businessImgList(list) {
  let html = '';
  list.forEach((value) => {
    html += `<div class="img" style="background-image: url(${value.imgUrl})"></div>`;
  })
  return html;
}

//初始化swiper
/*eslint-disable no-unused-vars, no-use-before-define */
const tabSwiper = new Swiper('#businessService', {
  initialSlide: 0,
  speed: 250,
  onSlideChangeStart(swiper) {
    tabsSelect(swiper.activeIndex, false);
  }
})
/*eslint-enable */

//tabs选择
function tabsSelect(index, swiper = true) {
  if (swiper) {
    tabSwiper.swipeTo(index, 250, false);
  }
  const labelElem = tabItemElem[index].querySelector('.label');
  const left = util.getElementLeft(labelElem);
  // const height = tabSlide[index].clientHeight;
  tabLineElem.style.left = left - 5 + 'px';
  tabLineElem.style.width = labelElem.clientWidth + 10 + 'px';
  // businessServiceElem.style.height = height + 'px';
  if (index === 1) {
    telWrapElem.classList.remove('hidden');
  } else {
    telWrapElem.classList.add('hidden');
  }
}

//绑定tabs点击事件
for (let j = 0; j < 3; j++) {
  tabItemElem[j].addEventListener('click', () => {
    tabsSelect(j);
  })
}

//优惠券
function cardsInit(list) {
  let html = '';
  if (list.length > 0) {
    html = '<div class="weui_cells">';
    list.forEach((value) => {
      html += `
        <div class="weui_cell">
          <div class="weui_cell_hd">
            <div class="card">
              <img class="img" src="img/card.png" />
              <div class="business">
                <span class="businessIcon"></span>
                <div class="businessName">${value.busName}</div>
              </div>
              <div class="money">
                <div class="num">${value.money}元</div>
              </div>
            </div>
          </div>
          <div class="weui_cell_bd weui_cell_primary">
            <div class="info">
              <div>有效期至：</div>
              <div>${value.eTime.substr(0, 10)}</div>
              <a class="getCard" href="${downloadLink}">领取票券</a>
            </div>
          </div>
          <div class="weui_cell_ft"></div>
        </div>
      `
    })
    html += '</div>';
  } else {
    html = '<img class="noData" src="img/noData.png" />';
  }
  return html;
}

//精品购
function goodsInit(list) {
  let html = '';
  if (list.length > 0) {
    html = '<div class="weui_cells">';
    list.forEach((value) => {
      html += `
        <div class="weui_cell">
          <div class="weui_cell_hd">
            <div class="goodsImg" style="background-image: url('${value.logo}')"></div>
          </div>
          <div class="weui_cell_bd weui_cell_primary">
            <div class="goodsName">${value.title}</div>
            <div class="tags"><i class="tagIcon"></i>${value.tags}</div>
            <div class="info">
              <div class="expFee">
                <span class="label">配送</span>
                <span class="text">${value.expFee === 0 ? '免费' : value.expFee + '元'}</span>
              </div>
              <div class="sales">
                <span class="label">销量</span>
                <span class="text">${value.sales}</span>
              </div>
              <div class="clicks">
                <span class="label">人气</span>
                <span class="text">${value.clicks}</span>
              </div>
            </div>
          </div>
          <div class="weui_cell_ft"></div>
        </div>
      `
    })
  } else {
    html = '<img class="noData" src="img/noData.png" />';
  }
  return html;
}

//获取页面内容
fetch(`${apiUrl}H5ShareInterface/ServiceCenter`, {
  method: 'post',
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
  },
  body: `serviceId=${serviceId}`
}).then((response) => {
  return response.json();
}).then((result) => {
  if (result.State === 0) {
    console.log(result);
    const { logo, nickName, address, isHot, isFamous, isFree, isReturn, isAuthentic,
      description, imgList, serviceContent, cards, goods, servicePhone } = result.Data;
    document.getElementById('logo').style.backgroundImage = `url(${logo})`;
    document.getElementById('nickName').textContent = nickName;
    document.getElementById('address').textContent = address;
    document.getElementById('hot').classList.add(isHot ? 'hotOn' : 'hot');
    document.getElementById('famous').classList.add(isFamous ? 'famousOn' : 'famous');
    document.getElementById('free').classList.add(isFree ? 'freeOn' : 'free');
    document.getElementById('return').classList.add(isReturn ? 'returnOn' : 'return');
    document.getElementById('authentic').classList.add(isAuthentic ? 'authenticOn' : 'authentic');
    util.spriteAdaptation(document.getElementById('hot'), tagScale, window.spriteImgSize);
    util.spriteAdaptation(document.getElementById('famous'), tagScale, window.spriteImgSize);
    util.spriteAdaptation(document.getElementById('free'), tagScale, window.spriteImgSize);
    util.spriteAdaptation(document.getElementById('return'), tagScale, window.spriteImgSize);
    util.spriteAdaptation(document.getElementById('authentic'), tagScale, window.spriteImgSize);
    document.getElementById('desc').textContent = description;
    document.getElementById('businessImg').style.width = 8 * 120 - 15 + 'px';
    document.getElementById('businessImg').insertAdjacentHTML('beforeend', businessImgList(imgList));
    document.getElementById('tel').insertAdjacentHTML('beforeend', `<a class="phone" href="tel:${servicePhone}">${servicePhone}</a>`);
    if (serviceContent) {
      document.getElementById('serviceContent').textContent = serviceContent;
    } else {
      document.getElementById('serviceContent').innerHTML = '<img class="noData" src="img/noData.png" />';
    }
    document.getElementById('cards').insertAdjacentHTML('beforeend', cardsInit(cards));
    document.getElementById('mallGoods').insertAdjacentHTML('beforeend', goodsInit(goods));
    for (i = 0; i < 3; i++) {
      tabSlide[i].style.display = 'inline-block';
    }
    util.pageComplete();
    tabsSelect(0);
    const businessImgElem = document.getElementById('businessImg').querySelectorAll('.img');
    const swiperBox = document.getElementById('swiperBox');
    const imgUrlArray = [];
    const viewImage = document.getElementById('viewImage');
    imgList.forEach((value, index) => {
      imgUrlArray[index] = value.imgUrl;
    })
    const businessImgElemLength = businessImgElem.length;
    for (let j = 0; j < businessImgElemLength; j++) {
      businessImgElem[j].addEventListener('click', () => {
        swiperUtil.viewImg(imgList.length, swiperBox, imgUrlArray, viewImage);
      })
    }
    swiperUtil.hiddenView(viewImage, swiperBox);
  } else {
    Alert(result.Msg);
  }
  util.pageComplete();
  //绑定tabs点击事件
})
