function downloadAppInit(util) {
  const template = `<div class="appDownload downloadAppEnter" id="appDownload">
    <div class="logo" id="appDownloadLogo"></div>
    <div class="appDesc">入驻自家小区，和邻居抢红包！</div>
    <a class="justDownload" href="http://a.app.qq.com/o/simple.jsp?pkgname=com.linxin.lx">立即下载</a>
    <div class="closeModal" id="closeAppDownload"></div>
    <div class="clearfix"></div>
  </div>`;
  if (util.param('tip') !== 'undefined') {
    document.body.insertAdjacentHTML('beforeend', template);
    const appDownload = document.getElementById('appDownload');
    const logoElem = document.getElementById('appDownloadLogo');
    const closeAppDownload = document.getElementById('closeAppDownload');
    if (window.spriteImgSize) {
      util.spriteAdaptation(logoElem, 0.5);
      util.spriteAdaptation(closeAppDownload, 0.5);
    }
    closeAppDownload.addEventListener('click', () => {
      appDownload.style.display = 'none';
    });
  }
}

module.exports = downloadAppInit;
